.animated-element
{
	visibility: hidden;
	position: relative;
}
.fadeIn, .fadeOut, .scale, .slideRight, .slideRightBack, .slideRight200, .slideLeft, .slideLeftBack, .slideLeft50, .slideDown, .slideDownBack, .slideDown200, .slideUp, .slideUpBack, .width, .height
{
	animation-timing-function: cubic-bezier(.75, 0, .25, 1);	
	-webkit-animation-timing-function: cubic-bezier(.75, 0, .25, 1);		
	visibility: visible !important;	
}
.slideRightBack, .slideLeftBack, .slideDownBack, .slideUpBack, .fadeOut
{
	opacity: 0;
}
/* --- fadeIn --- */
.fadeIn
{	
	animation-name: fadeIn;
	-webkit-animation-name: fadeIn;	
}
@keyframes fadeIn 
{
	0% 
	{
		opacity: 0;
	}
	100% 
	{
		opacity: 1;
	}
}
@-webkit-keyframes fadeIn 
{
	0% 
	{
		opacity: 0;
	}
	100% 
	{
		opacity: 1;
	}
}
/* --- fadeOut --- */
.fadeOut
{	
	animation-name: fadeOut;
	-webkit-animation-name: fadeOut;	
}
@keyframes fadeOut 
{
	0% 
	{
		opacity: 1;
	}
	100% 
	{
		opacity: 0;
	}
}
@-webkit-keyframes fadeOut 
{
	0% 
	{
		opacity: 1;
	}
	100% 
	{
		opacity: 0;
	}
}
/* --- scale --- */
.scale
{
	animation-name: scale;
	-webkit-animation-name: scale;
}
@keyframes scale 
{
	0% 
	{
		transform: scale(0.2);	
	}
	100% 
	{
		transform: scale(1);	
	}		
}
@-webkit-keyframes scale 
{
	0% 
	{
		-webkit-transform: scale(0.2);	
	}
	100% 
	{
		-webkit-transform: scale(1);
	}		
}
